
var XLSX = require('xlsx')

function s2ab (s) { // 
    var buf = new ArrayBuffer(s.length)
    var view = new Uint8Array(buf)
    for (var i = 0; i !== s.length; ++i) {
      view[i] = s.charCodeAt(i) & 0xFF
    }
    return buf
  }


export  function downLoadExcel (json, downName,dom, type) {  // excel
    let keyMap = [] // 
    for (let k in json[0]) {
      keyMap.push(k)
    }
    let tmpdata = [] // json
    json.map((v, i) => keyMap.map((k, j) => Object.assign({}, {
      v: v[k],
      position: (j > 25 ? this.getCharCol(j) : String.fromCharCode(65 + j)) + (i + 1)
    }))).reduce((prev, next) => prev.concat(next)).forEach(function (v) {
      tmpdata[v.position] = {
        v: v.v
      }
    })
    let outputPos = Object.keys(tmpdata)  // ,A1D10
    let tmpWB = {
      SheetNames: ['mySheet'], // 
      Sheets: {
        'mySheet': Object.assign({},
          tmpdata, // 
          {
            '!ref': outputPos[0] + ':' + outputPos[outputPos.length - 1] // 
          })
      }
    }
    let tmpDown = new Blob([s2ab(XLSX.write(tmpWB,
      {bookType: (type === undefined ? 'xlsx' : type), bookSST: false, type: 'binary'} // 
    ))], {
      type: ''
    })  // 
    var href = URL.createObjectURL(tmpDown)  // 
   dom.download = downName + '.xlsx'  // 
   dom.href = href  // a
   dom.click()  // 
    setTimeout(function () {  // 
      URL.revokeObjectURL(tmpDown) // URL.revokeObjectURL()object URL
    }, 100)
  }
  


